/* Item text */

.textContentItem {
  font-size: 16px;
  word-break: break-all;
}

.viewListContentChat div:last-child {
  margin-bottom: 10px;
}

/* Item image */

.imgItemRight {
  object-fit: cover;
  width: 300px;
  height: 300px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 0px;
}

.imgItemLeft {
  object-fit: cover;
